img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


.page__body {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 18px;
  background-color: #ffffff;
  font-weight: 400;
  color: #000000;
  margin: 0;
}

.title {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 40px;
  color: inherit;
  font-weight: 400;
}
